import React from 'react';
import PropTypes from 'prop-types';
import './style.scss';

/**   chip
 * @children : chip text
 */
const AdviseFieldChip = ({ children }) => {
  let chipStyle;

  if (children === 'A/S') chipStyle = { backgroundColor: '#ECEDEF' };
  if (children === '') chipStyle = { backgroundColor: '#FCEBDB' };
  if (children === ' ') chipStyle = { backgroundColor: '#D9E7FF' };
  if (children === '  ') chipStyle = { backgroundColor: '#F4EFFA' };
  if (children === '') chipStyle = { backgroundColor: '#E0E5EE' };
  if (children === '') chipStyle = { backgroundColor: '#E1F5EA' };
  if (children === '') chipStyle = { backgroundColor: '#D8F0F8' };
  if (children === '') chipStyle = { backgroundColor: '#FEECEB' };
  if (children === '') chipStyle = { backgroundColor: '#EDF2FF' };
  if (children === '') chipStyle = { backgroundColor: '#EFF8DC' };
  if (children === '') chipStyle = { backgroundColor: '#FFECC9' };

  return (
    <div className="advise_field_color_chip" style={chipStyle}>
      #{children}
    </div>
  );
};

AdviseFieldChip.propTypes = {
  children: PropTypes.any,
};

AdviseFieldChip.defaultProps = {
  children: '',
};

export default AdviseFieldChip;
